.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
  text-align:center;
  
}

.App-intro {
  font-size: large;
}

.App-container {
  margin:0 auto;
  width: 100%;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.container {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.flex-row {
  -ms-flex-direction: row;
      flex-direction: row;
  display: -ms-flexbox;
  display: flex;
}

.flex-half {
  -ms-flex: 1 0 50%;
      flex: 1 0 50%;
}

.posts {
  margin: 0px 15%;
}
.posts div {
  margin: 10px 0px;
}
.posts h1{
  font-size: 30px;
  font-weight:900;
  text-transform: capitalize;
}

.posts h4 {
  font-size:14px;
  color: #777
}

.strike {
  text-decoration: line-through;
}


